@import "src/global"

.service-info
  width: 100%
  min-width: px(300)
  padding: px(32) px(16)
  background-color: var(--gary-0)
  border-radius: px(18)
  display: flex
  flex-direction: column
  align-items: start
  border: px(1) solid var(--gary-1)

  &__icon
    height: px(88)
    margin-bottom: auto

  &__title
    @include header-64
    padding-bottom: px(16)
    border-bottom: px(1) solid var(--gary-1)
    margin-top: px(40)
    margin-bottom: px(40)
    width: 100%

  &__content
    margin-bottom: px(40)
    user-select: text

    h3
      margin: px(24) 0 px(8)
      @include header-18

      &:first-child
        margin-top: 0

      &:last-child
        margin-bottom: 0

    p
      color: var(--gary-3)
      margin: px(8) 0
      @include text-14

      &:first-child
        margin-top: 0

      &:last-child
        margin-bottom: 0