@import "src/global"

.home-page
  position: absolute
  left: 0
  top: px(60)
  height: calc(100vh - #{px(60)})
  padding: px(36) px(48)
  padding-bottom: px(72)
  width: 100%
  overflow: hidden
  overflow-x: scroll
  scrollbar-width: none

  &_has-hello &__hello
    display: flex

  &::-webkit-scrollbar
    height: px(2)

  &::-webkit-scrollbar-thumb
    background-color: transparent
    transition: 0.15s ease
    border-radius: px(999)

  &_scroll-visible
    &::-webkit-scrollbar-thumb
      background-color: var(--gary-2)

  @media screen and (min-width: px(1200))
    &__services + &__empty
      margin-left: px(-36)

  &__empty
    position: relative
    width: 100%
    overflow: hidden
    border: px(1) var(--gary-1) solid
    border-radius: px(16)
    padding: px(40) px(24)
    min-width: px(1000)

    &-title
      @include header-128
      margin-bottom: px(48)

      &-mobile
        display: none

    &-text
      @include text-20
      color: var(--gary-4)
      margin-bottom: px(40)
      max-width: px(500)

    &-image
      position: absolute !important
      height: 100%
      right: 0
      top: 0
      z-index: -1
      mix-blend-mode: lighten

    &:before
      content: ""
      display: block
      position: absolute
      width: 100%
      height: 100%
      top: 0
      right: 0
      z-index: -2
      background-color: var(--gary-0)
      //opacity: 0.5

    @media screen and (max-width: px(1200))
      min-width: auto
      width: 100%

      &-title
        @include header-92

    @media screen and (max-width: px(600))
      height: px(450)
      display: flex
      flex-direction: column
      padding-left: px(16)
      padding-right: px(16)
      display: none

      &-image
        display: none

      &-button
        margin-top: auto
        width: 100%

      &-text
        @include text-18

      &-title
        @include header-64

        &-desktop
          display: none

        &-mobile
          display: block

  &__hello-background
    position: absolute !important
    top: 0
    left: 0
    height: 100%
    z-index: -1
    mix-blend-mode: lighten

  &__hello-background-for-tablet
    position: absolute !important
    top: 0
    right: 0
    height: calc(100% + #{px(36)})
    z-index: -1
    mix-blend-mode: lighten
    margin-top: px(-36)
    margin-right: px(-48)
    display: none

  &__hello-image-for-mobile
    display: none
    margin-left: px(-16)
    margin-right: px(-16)
    width: 100vw
    margin-top: auto

  &__tags-place
    display: none
    margin-top: px(16)
    padding-bottom: px(16)
    border-bottom: px(1) solid var(--gary-1)
    max-width: 100%

    &-title
      margin-bottom: px(32)
      @include header-64

    &-list
      display: flex
      flex-wrap: wrap
      gap: px(8)
      max-width: 100%

  &__services
    display: flex
    gap: px(16)

  &__service
    max-width: px(300)
    min-width: px(300)

  &__hello
    min-width: px(720)
    display: none
    flex-direction: column

    &-title
      margin-bottom: px(40)
      @include header-128

      &-desktop
        display: inline

      &-mobile
        display: none

    &-sub-title
      max-width: px(480)
      margin-bottom: px(40)
      color: var(--gary-3)
      @include text-20

  &__wrapper
    display: flex
    gap: px(72)
    min-width: fit-content
    height: 100%

  &__cases
    display: flex
    gap: px(72)
    min-width: fit-content
    padding-right: px(48)
    width: fit-content

  &__case
    height: 100%

  @media screen and (max-width: px(1200))
    margin-top: px(60)
    position: static
    height: fit-content
    padding-bottom: px(36)

    &__hello-background
      display: none

    &__hello-image-for-mobile
      display: none

    &__hello-background-for-tablet
      display: block

    &__tags-place
      display: flex
      flex-direction: column

    &__services
      width: 100vw
      overflow-x: auto
      margin-left: px(-16)
      margin-right: px(-16)
      padding-left: px(16)
      padding-right: px(16)
      padding-bottom: px(16)
      position: relative

      &:before
        content: ""
        position: absolute
        bottom: 0
        left: px(16)
        right: px(16)
        border-bottom: px(1) solid var(--gary-1)

      &::-webkit-scrollbar
        display: none

    &__hello
      min-width: 0
      padding-bottom: px(72)
      padding-top: px(24)
      border-bottom: px(1) solid var(--gary-1)
      position: relative
      display: flex

    &__wrapper
      flex-direction: column
      gap: px(36)
      width: 100%

    &__cases
      display: grid
      grid-template-columns: 1fr 1fr
      padding: 0
      gap: px(128) px(72)

    &__case:nth-child(3n)
      grid-column: 1/3
      width: 100%

  @media screen and (max-width: px(600))
    padding: px(32) px(16)

    &__hello-background
      display: none

    &__hello-image-for-mobile
      display: block

    &__hello-background-for-tablet
      display: none

    &__case:nth-child(3n)
      grid-column: 1/2

    &__hello
      padding-top: 0
      height: calc(100svh - #{px(100)} - #{px(32)})
      display: flex
      flex-direction: column
      padding-bottom: px(16)
      border-bottom: px(1) solid var(--gary-1)

      &-image-for-mobile
        display: block

      &-title
        margin: 0
        @include header-92
        font-size: 24vw

        &-desktop
          display: none

        &-mobile
          display: inline

      &-sub-title
        margin: auto 0 px(32)
        @include text-18

        br
          display: none

      &-button
        width: 100%

    &__wrapper
      flex-direction: column
      gap: px(32)

    &__cases
      grid-template-columns: 1fr
      gap: px(32)

    &__case
      padding-bottom: px(16)
      border-bottom: px(1) solid var(--gary-1)

.footer_in-home-page
  position: fixed
  bottom: 0

  @media screen and (max-width: px(1200))
    position: static