@import "src/global"

.button
  position: relative
  width: fit-content
  height: px(36)
  display: flex
  align-items: center
  justify-content: center
  gap: px(4)
  padding: px(0) px(12)
  background-color: var(--background)
  cursor: pointer
  border-radius: px(999)
  white-space: nowrap
  @include text-14
  @include button-hover

  &__icon
    min-width: px(28)
    height: px(28)
    display: flex
    background-size: contain

    &_at-left
      margin-left: px(-8)
      margin-right: px(4)

    &_at-right
      margin-right: px(-8)
      margin-left: px(4)

  &_only-icon
    padding: 0 px(4)

  &_only-icon &__icon

    &_at-left
      margin-left: 0
      margin-right: 0

    &_at-right
      margin-right: 0
      margin-left: 0

  @media screen and (min-width: px(1200))
    &_small
      height: px(28)

    &_small &__icon

      &_at-left
        margin-left: px(-12)
        margin-right: auto

      &_at-right
        margin-right: px(-12)
        margin-left: auto

    &_only-icon.button_small &__icon
      margin: 0

    &_only-icon.button_small
      padding: px(0)

  &_big
    height: px(56)
    border-radius: px(14)
    gap: px(16)
    padding-left: px(24)
    padding-right: px(24)
    @include text-20

    &:before
      border-radius: px(14)

  &_big &__icon

    &_at-left
      margin-left: px(0)
      margin-right: px(0)

    &_at-right
      margin-right: px(0)
      margin-left: px(0)

  &_only-icon.button_big &__icon
    margin: 0

  &_only-icon.button_big
    padding: px(0)