@import "src/global"

.hat
  position: fixed
  top: 0
  left: 0
  width: 100%
  height: px(60)
  background-color: var(--background)
  border-bottom: px(1) solid var(--gary-1)
  display: flex
  justify-content: space-between
  align-items: end
  gap: px(4)
  padding: px(12) px(48)
  z-index: 1000
  overflow: hidden

  &__logo
    min-width: px(42)
    height: px(34)
    display: flex
    align-items: center
    justify-content: center
    margin-right: px(48)

    &-image
      min-width: px(42)
      height: px(42)

  &__menu
    &-some-buttons
      display: flex
      align-items: center
      gap: px(4)

    &-one-button
      position: relative
      display: none
      height: px(36)
      max-width: px(128)
      min-width: px(128)
      border: px(1) solid var(--gary-1)
      border-radius: px(999)
      padding-left: px(20)
      padding-right: px(20)
      align-items: center
      margin-right: px(20)
      cursor: pointer
      background-color: var(--gary-1)

      &:after
        content: ""
        display: block
        position: absolute
        height: px(11)
        width: calc(100% - #{px(40)})
        border-top: px(1) solid var(--white)
        border-bottom: px(1) solid var(--white)

      &:before
        content: ""
        display: block
        height: px(1)
        width: 100%
        background-color: var(--white)

      &-lines-in-close-state
        display: none

        &:after
          content: ""
          display: block
          position: absolute
          transform: rotate(45deg)
          height: px(1)
          width: px(24)
          background-color: var(--white)
          right: px(32)
          border: solid px(1) var(--background)
          z-index: 1
          margin-top: px(-1)

        &:before
          content: ""
          display: block
          position: absolute
          transform: rotate(45deg)
          height: px(1)
          width: px(24)
          background-color: var(--white)
          left: px(32)
          border: solid px(1) var(--background)
          z-index: 1
          margin-top: px(-1)

      &-notification-point
        position: absolute
        display: block
        right: 0
        top: 0
        width: px(12)
        height: px(12)
        background-color: var(--white)
        border-radius: px(999)
        border: px(2) solid var(--background)

  &__separator
    height: px(24)
    min-width: px(1)
    background-color: var(--gary-1)
    margin-left: px(8)
    margin-right: px(8)

  &__cases-tags-swiper
    width: 100%
    position: relative

    &-navigation-button
      position: absolute
      top: 0
      z-index: 10
      opacity: 0

      &_prev
        left: 0

      &_next
        right: 0

    &:hover &-navigation-button
      opacity: 1

      &_prev
        left: 0

      &_next
        right: 0

    &:after
      content: ""
      display: block
      position: absolute
      right: 0
      top: 0
      height: 100%
      width: px(96)
      background: linear-gradient(90deg, #00000000, var(--background))
      z-index: 5
      pointer-events: none

    &:before
      content: ""
      display: block
      position: absolute
      left: 0
      top: 0
      height: 100%
      width: px(96)
      background: linear-gradient(-90deg, #00000000, var(--background))
      z-index: 5
      pointer-events: none

    &_at-end:after
      opacity: 0

    &_at-end &-navigation-button:last-of-type
      opacity: 0

    &_at-start:before
      opacity: 0

    &_at-start &-navigation-button:first-of-type
      opacity: 0

  &__cases-tag-slide
    width: fit-content
    padding-right: px(4)

    &:last-child
      padding-right: px(0)

  &__social-network
    display: flex
    gap: px(4)
    min-width: fit-content
    margin-left: px(48)

  &__cases-tag
    background-color: var(--background)

  //Mobile menu
  &__mobile-menu
    display: flex
    position: fixed
    width: 100%
    height: 100%
    left: 0
    bottom: 0
    z-index: 500
    padding: px(40) 0
    backdrop-filter: blur(px(48))
    flex-direction: column
    gap: px(32)
    background: linear-gradient(var(--background) px(256), rgba(0, 0, 0, 0) 100%)
    opacity: 0
    pointer-events: none
    transition: 0.5s ease opacity, 0.25s ease transform
    padding-top: px(100)
    transform: translateY(px(60))
    z-index: -1

    &-button
      @include header-64
      text-decoration: none
      padding: 0 px(48)
      max-width: px(400)

    &-tags
      flex-wrap: wrap
      gap: px(4)
      border: solid px(1) var(--gary-1)
      padding: px(24) px(16)
      border-radius: px(16)
      border-right: 0
      border-left: 0
      position: relative

      &:before
        position: absolute
        content: ""
        display: block
        transform: rotate(45deg)
        border: solid px(1) var(--gary-1)
        border-right: 0
        border-bottom: 0
        width: px(8)
        height: px(8)
        top: px(-5)
        left: px(48)
        background-color: var(--background)

  &_mobile-menu-is-opened &__menu-one-button

    &-lines-in-close-state
      display: block

  @media screen and (max-width: px(1200))
    &_mobile-menu-is-opened
      border-bottom: 0

  @media screen and (max-width: px(1200))

    &_mobile-menu-is-opened &__mobile-menu
      opacity: 1
      pointer-events: fill
      transform: translateY(px(0))

    &__separator
      display: none

    &__logo
      margin-right: px(24)

    &__menu
      width: 100%

      &-some-buttons
        display: none

      &-one-button
        display: flex

    &__cases-tags-swiper-navigation-button
      &_prev
        display: none

      &_next
        display: none

    &__social-network
      margin-left: px(24)

    &__cases-tags-swiper
      display: none

  @media screen and (max-width: px(600))
    padding: px(12) px(16)
    gap: px(24)

    &__mobile-menu
      background: linear-gradient(var(--background) 50%, rgba(0, 0, 0, 0) 100%)

      &-button
        padding: 0 px(16)

      &-tags
        display: flex

    &__menu
      display: flex
      justify-content: center
      width: 100%
      flex-grow: 1

      &-one-button
        width: 100%
        margin: 0
        min-width: auto
        max-width: px(256)

    &__logo
      margin-right: 0

    &__cases-tags-swiper
      display: none

    &__social-network
      margin-left: 0

    &__telegram-button
      display: none
