@import "src/global"

.case
  display: grid
  grid-template-rows: 1fr auto
  grid-template-columns: auto
  gap: px(24)
  width: fit-content
  position: relative

  &__image
    border-radius: px(4)
    min-height: 0
    margin-top: auto
    height: 100%
    opacity: 0

  &__title
    margin-bottom: px(4)
    user-select: text
    @include header-18

  &__description
    color: var(--gary-3)
    margin-bottom: px(16)
    user-select: text
    @include text-14

  &__buttons
    display: flex
    gap: px(4)

  &__preloader
    position: absolute
    width: px(100)
    height: px(100)
    left: calc(50% - #{px(100)}/2)
    top: calc(50% - #{px(100)}/2)

  &:before
    content: ""
    position: absolute
    z-index: -1
    width: 100%
    height: 100%
    left: 0
    top: 0
    //background-color: var(--gary-0)
    filter: blur(px(64))
    display: block

  &_downloaded-image &__preloader
    opacity: 0

  &_downloaded-image:before
    display: none

  &_downloaded-image &__image
    opacity: 1

  &_downloaded-threeDCase &__preloader
    opacity: 0

  &_downloaded-threeDCase &__image
    opacity: 0

  &_downloaded-threeDCase:before
    display: none

  @media screen and (max-width: px(1200))

    &__image
      width: 100%

  @media screen and (max-width: px(600))

    &__link-button
      margin-left: auto