@import "/src/global.sass"

.footer
  width: 100%
  min-height: px(40)
  display: flex
  align-items: stretch
  justify-content: space-between
  padding: px(4) px(48) px(4)
  flex-wrap: wrap-reverse
  height: fit-content
  gap: px(4)
  @include text-14
  margin-top: auto

  &__message
    color: var(--gary-2)
    background-color: var(--background)
    display: flex
    align-items: center
    padding: px(0) px(8)
    border-radius: px(4)

  &__copyright
    color: var(--white)
    background-color: var(--background)
    display: flex
    align-items: center
    padding: px(0) px(8)
    border-radius: px(4)

  @media screen and (max-width: px(600))
    align-items: start
    gap: px(16)
    flex-direction: column-reverse
    padding: px(14)
    height: fit-content
    border-top: none