.media-frame
  position: relative

  &__preloader-place
    position: absolute
    top: 0
    left: 0
    width: 100%
    height: 100%
    display: flex
    align-items: center
    justify-content: center

  &__video, &__image
    width: 100%
    height: 100%
    pointer-events: none
    transition: 0.15s ease opacity
    opacity: 0
    object-fit: cover

  &_video &__image
    position: absolute
    top: 0
    left: 0

  &_image-downloaded &__image
      opacity: 1

  &_video-downloaded &__image
    opacity: 0

  &_video-downloaded &__video
    opacity: 1